            <p className={SheetStyles.paragraph}>
              Il potere di una strategica pianificazione strutturale degli
              elementi narrativi non  rilevante solo sul piano dellanalisi
              comparata tra due testi: uno sguardo attento sulle scelte fatte da
              Calvino nel distribuire i suoi materiali pu rivelarci dettagli
              illuminanti su come viene costruito lequilibrio interno di una
              storia. Un esempio  la funzione dellincontro come meccanismo di
              dissimulazione della complessit. Sia nella <em>Nuvola di smog</em> che
              nella <em>Formica argentina</em>, lossatura fondamentale del testo 
              basata sulla stessa dinamica: un protagonista che parla in prima
              persona ma non ha nome n volto si muove tra uno stuolo di
              personaggi minori ognuno dei quali ha un suo modo di contrapporsi
              alle formiche o allo smog (Falcetto 1991, 1313), con un modello
              che riprende, da un lato, il modello dantesco del viaggio
              nelloltretomba popolato da anime-<em>exempla</em>, e dallaltro la forma
              del <em>Bildungsroman</em> goethiano, in cui leroe  avviato a un percorso
              di crescita la cui riuscita dipende dallinsegnamento che sa
              estrarre dai personaggi che incrocia. Ma nelle pieghe di questa
              modalit a prima vista elementare, che scandisce il testo in pochi
              lunghi blocchi ricorrenti, si annida un intero repertorio di
              situazioni, sentimenti, scoperte, incontri negli incontri:
              linterazione con un altro individuo non  soltanto una delle
              occasioni che possono darsi nella vicenda dei protagonisti, ma un
              momento di innesco che moltiplica gli eventi narrativi pur
              tenendoli tutti al suo interno. Una tattica su cui Calvino 
              capace di costruire un effetto nelleffetto, come succede nella
              seconda parte della <em>Formica</em>, dove esibisce un gusto combinatorio
              gi molto simile per sensibilit alle opere <em>oulipienne</em> degli
              anni Settanta. In queste pagine, infatti, vediamo ben tre esempi
              consecutivi di serie perfette, o quasi, vale a dire tre casi in
              cui la narrazione assume una struttura compiutamente modulare e
              ripete intenzionalmente frammenti di se stessa, tutti imperniati
              sullelemento dellincontro. Nellordine, la prima  una serie di
              due ripetizioni della sequenza
              incontro-iniziativa-speranza-delusione (il protagonista
              interagisce con un personaggio e individua un piano per affrontare
              il problema dellinfestazione, attraversa un momento di euforia, e
              poi perde fiducia e sprofonda nellangoscia per il probabile
              insuccesso); la seconda  una serie di tre ripetizioni della
              sequenza incontro-delusione (il protagonista cerca di consultarsi
              con un altro personaggio ma lincomunicabilit e il contatto
              fallito lo gettano in uno stato di depressione); e infine la terza
               di nuovo una serie di due ripetizioni, ma stavolta con una
              variazione: incontro-iniziativa-delusione e
              incontro-iniziativa-<em>visione</em>, con la ricercata rottura dello schema
              che acutizza il senso di disperazione delleroe ormai vicino al
              punto di rottura. Non si tratta ovviamente solo di un piccolo
              gioco nascosto dallautore dietro le quinte della storia:
              leffetto di ripetizione  integrale alla rappresentazione del
              pessimismo incombente del racconto, che moltiplica gli sforzi
              degli individui perseguitati dalle formiche per sottolineare
              ancora pi nettamente la loro inutilit. Ma  anche funzionale a
              rivelare la lucidit disillusa del protagonista: se infatti un
              singolo incontro, con una singola raccomandazione strategica,
              potrebbe costituire un punto di speranza, tenacia, e di sfida
              alluniverso, facendo apparire la tendenza del protagonista allo
              scoraggiamento come un tratto di debolezza, la girandola degli
              incontri e dei suggerimenti disparati trasmette immediatamente la
              sensazione di una vuota illusione e di un disperato attaccamento a
              qualche menzogna consolatoria, di fronte alla quale il realismo
              stoico e disincantato del protagonista, che tenta tutto senza
              convinzione, rimane a campeggiare come lunica basata su una
              disposizione danimo condivisibile: la dignitosa rassegnazione
              alla presenza del male nelluniverso e il coraggio della realt
            </p>
